export default {
    name: "LiveHead",
  data(){
    return {
      searchStr: '',
      headImg: '',
    }
  },
  props:['room_name'],
  created(){
    if(this.room_name){
      this.searchStr = this.room_name;
    }
    var uimg = localStorage.getItem('uimg');
    if( uimg && typeof(uimg) != "undefined" ){
      this.headImg = uimg;
    }
  },
  methods:{
    searchRoom(){
      if(this.searchStr){
        this.$router.replace({path: '/', query:{room_name: this.searchStr}})
        this.$emit('searchRoom');
      } else {
        this.$vux.alert.show({
          title: '温馨提示',
          content: '请填写直播间名称'
        });
      }
    },
    goHome(){
      this.$router.replace({path: '/'});
    },
  }
}



