import LiveUserHead from './LiveUserHead'
import LiveFooter from './LiveFooter'
  export default {
    name: "LiveUser",
    components: {LiveUserHead, LiveFooter},
    data(){
        return {
          'findex': 'user',
          'uitem': 0,
        }
    },
    created(){

    },
    methods:{
      goDetail(utype){
        this.uitem = utype;
        switch (utype) {
          case 1: //订单列表
            this.$router.replace({path: '/orders'});
            break;
          case 2: //地址列表
            this.$router.replace({path: '/addr'});
            break;
          case 3: //我的关注
            this.$router.replace({path: '/roomStart'});
            break;
          case 4: //开通直播
            break;
          case 5: //我要合作
            break;
          case 6: //客服中心
            break;
        }
      }
    }
  }



