/* harmony default export */ var Product = ({
    name: "product",
    props: ['room_id'],
    data: function data() {
        return {
            openid: "",
            productList: [],
            totalPrice: 0
        };
    },
    created: function created() {
        this.openid = localStorage.getItem('openid');
        this.getData();
    },

    methods: {
        getData: function getData() {
            var _this = this;

            var that = this;

            that.$vux.loading.show({
                text: '加载中~'
            });

            var formdata = new FormData();
            that.axiosPost("/product/room?id=" + this.room_id, formdata).then(function (res) {
                that.$vux.loading.hide();
                if (res.status == 200) {
                    that.productList = res.data;
                } else {
                    _this.$vux.alert.show({
                        title: '温馨提示',
                        content: res.message });
                }
            }, function (err) {
                that.$vux.loading.hide();
            });
        },
        changeNum: function changeNum(type, item) {

            if (type == 1) {
                // 减少
                item.buy_num--;
                if (item.buy_num < 0) {
                    item.buy_num = 0;
                }
            } else {
                // 增加
                item.buy_num++;
            }

            var buy_price = 0;
            this.totalPrice = 0;
            for (var i = 0; i < this.productList.length; i++) {
                buy_price += this.productList[i].buy_num * this.productList[i].price;
                if (buy_price) {
                    this.totalPrice = buy_price.toFixed(2);
                }
            }

            this.$emit('buy_price', this.totalPrice);
        },
        buy_product: function buy_product(room_id, room_name, room_logo, room_tel) {
            var buy_product = [];

            var total = 0;
            for (var i = 0; i < this.productList.length; i++) {
                if (this.productList[i].buy_num > 0) {
                    total += this.productList[i].buy_num * this.productList[i].price;
                    buy_product.push(this.productList[i]);
                }
            }
            total = total.toFixed(2);
            if (buy_product.length > 0) {
                var room_info = {
                    'room_id': room_id,
                    'room_name': room_name,
                    'room_logo': room_logo,
                    'room_tel': room_tel
                };

                localStorage.setItem("buy_room", stringify_default()(room_info));
                localStorage.setItem('buy_total', total);
                localStorage.setItem('buy_product', stringify_default()(buy_product));
                this.$router.replace({ path: '/order', query: { room_id: room_id } });
            } else {
                this.$vux.alert.show({
                    title: '温馨提示',
                    content: "请选择需要购买的商品" });

                return false;
            }
        }
    }
});
