/* harmony default export */ var OrderSuccess = ({
    name: 'order-success',
    data: function data() {
        return {
            orders: {},
            order_id: '',
            openid: '',
            tel: ''
        };
    },
    created: function created() {
        this.order_id = this.$route.query.order_id;
        this.getOrderList();
    },

    methods: {
        myOrderListPath: function myOrderListPath() {
            this.$router.replace({ path: '/order/mylist' });
        },
        homePath: function homePath() {
            var tmp_room_id = localStorage.getItem('roomid');
            //this.$router.push({path: '/room', query:{room_id:tmp_room_id}})

            if (this.DEBUG == 1) {
                this.$router.replace({ path: '/room', query: { room_id: tmp_room_id, from: "groupmessage", isappinstalled: 0 } });
            } else {
                window.location.href = location.protocol + '//' + document.domain + '/front/#/room?room_id=' + tmp_room_id;
            }
        },
        getOrderList: function getOrderList() {
            var that = this;
            if (!that.order_id) {
                this.$vux.alert.show({
                    title: '温馨提示',
                    content: "无效订单号" });
                return false;
            }

            var formdata = new FormData();
            formdata.append('order_id', that.order_id);

            that.axiosPost('/client/orders', formdata).then(function (res) {
                that.orders = res.data[0];
                that.tel = that.orders.mobile ? "tel:" + that.orders.mobile : "";
            }, function (err) {
                console.log(err);
            });
        }
    }
});
