            switch (index) {
                case 0:
                    $('.shoppingCarBox').hide();
                    $('.sendMessMenu').hide();
                    break;
                case 1:
                    $('.shoppingCarBox').fadeIn();
                    $('.sendMessMenu').hide();
                    break;
                case 2:
                    $('.sendMessMenu').fadeIn();
                    $('.shoppingCarBox').hide();
                    break;
                case 3:
                    $('.shoppingCarBox').hide();
                    $('.sendMessMenu').hide();
                    break;
                default:
            }
