export default {
  name: 'app',
  provide (){
    return {
      reloadPage:this.reloadPage
    }
  },
  data(){
    return {
      isRouterAlive:true
    }
  },
  methods:{
    reloadPage (){
      this.isRouterAlive = false
      this.$nextTick(function(){
        this.isRouterAlive = true
      })
    }
  },
}



