export default {
  name: "LiveUserHead",
  data(){
    return {
      userInfo: {},
      headImg:'../../static/images/defaultUser.png',
      userName: '云窗在线',
    };
  },
  created(){
    var uimg = localStorage.getItem('uimg');
    if( uimg && typeof(uimg) != "undefined" ){
      this.headImg = uimg;
    }

    var uname = localStorage.getItem('uname');
    if(uname && typeof(uname) != "undefined" ){
      this.userName = uname;
    }

    this.getData();
  },
  methods:{
    getData(){

    }
  }

}



