export default {
  name: "LiveFooter",
  props:['findex'],
  data(){
    return {
      'footer': [
        {
          fname: 'live',
          fclass: 'live-foot-live',
          ftext: '视频现场',
          path: '/',
        },
        {
          fname: 'video',
          fclass: 'live-foot-video',
          ftext: '视频现场',
          path: '/video',
        },
        {
          fname: 'user',
          fclass: 'live-foot-user',
          ftext: '个人中心',
          path: '/user'
        },
      ],
    }
  },
  methods:{
    changeIndex(item){
      this.$router.replace({path: item.path});
    }
  },
}



